html {
	font: normal 18px/1.3 Georgia, "New York CE", utopia, serif;
	color: #666;
	-webkit-text-stroke: 1px rgba(0,0,0,0);
	overflow-y: scroll; }

body {
	background: #3484d2;
	color: #333;
	margin: 2em auto;
	padding: 0 .5em;
	max-width: 600px;
	min-width: 320px; }

a {
	color: #006aeb;
	padding: 3px 1px;

	&:hover, &:active, &:focus {
		text-decoration: none; } }

#banner {
		border-radius: 12px 12px 0 0;
		background-image: url(data:image/png;base64,); }

h1 {
	font: inherit;
	color: white;
	font-size: 50px;
	line-height: 121px;
	margin: 0;
	padding-left: 4%;
	background-size: 130px auto;
	text-shadow: 1px 1px 0 rgba(0, 0, 0, .9);

	&.job_position {
		font-size: 25px; } }

@media (max-width: 600px) {
	background: none;
	font-size: 40px; }

h2 {
	font: inherit;
	padding: 1.2em 0;
	margin: 0;
	font-size: 1.5rem; }

h3 {
	font: inherit;
	padding: 1.2em 0;
	margin: 0;
	font-size: 1.20rem; }

img {
	border: none;
	float: right;
	margin: 0 0 1em 3em; }

.job_position_location {
	padding: 20px 0; }

.container {
	background-color: white;
	display: inline-block; }

.form {
	margin-top: 25px;
	padding: 0 25px; }

p {
	&.bold {
		font-weight: bold; } }

.flash {
	&.info {
		font-weight: bold;
		padding: 20px;
		color: darkgreen;
		text-align: center; } }

#content {
	background: white;
	border: 1px solid #eff4f7;
	border-radius: 0 0 12px 12px;
	padding: 10px 4%;
	overflow: hidden; }

// Form - Dropzone Extension
#dropzoneExt {
	position: absolute;
	z-index: 2;
	height: 150px;
	cursor: pointer; }

.custom-file-label[for="dropzoneExt"] {
	border: 2px dashed rgba(0, 0, 0, 0.3);
	background: white;
	border-radius: 5px;
	height: 150px;
	padding: 54px 54px;
	cursor: pointer;
	position: relative;
	width: 100%;
	opacity: 1;

	&.hover {
		border-style: solid; }

	&::after {
		content: 'Drop files here or click to upload';
		position: absolute;
		color: #808080;
		background-color: transparent;
		top: 50%;
		left: 50%;
		padding: 0;
		border-radius: 0;
		border: none;
		line-height: normal;
		transform: translate(-50%, -50%);
		text-align: center; } }
